/* Стили для основной структуры страницы */
.main-content {
    padding-top: 80px; /* Отступ сверху, чтобы учесть фиксированный header */
    padding-bottom: 60px; /* Отступ снизу, чтобы учесть фиксированный footer */
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px; /* Промежуток между элементами внутри main-content */
    min-height: calc(100vh - 140px); /* Высота контента с учетом header и footer */
    background-color: #f8f9fa; /* Светлый фон для основного контента */
}

/* Адаптивный дизайн для небольших экранов */
@media (max-width: 768px) {
    .main-content {
        padding-top: 60px; /* Уменьшенный отступ для мобильных устройств */
        padding-bottom: 50px;
        gap: 30px;
    }
}
