.home-page {
    display: flex;
    flex-direction: column;
    height: 100vh;
  }
  
  .content {
    display: flex;
    flex: 1;
  }
  
  .chat-list {
    width: 25%;
    background-color: #f1f1f1;
    padding: 20px;
    border-right: 1px solid #ddd;
  }
  
  .chat-list h3 {
    margin: 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .chat-list ul {
    list-style-type: none;
    padding: 0;
  }
  
  .chat-list li {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    cursor: pointer;
  }
  
  .chat-list li:hover {
    background-color: #f9f9f9;
  }
  