.news-block {
    background-color: #f1f1f1;
    padding: 20px;
    margin-top: 100px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.news-block h2 {
    font-size: 22px;
    margin-bottom: 10px;
}

.news-block p {
    font-size: 16px;
    color: #333;
}
