.userEntity-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border-bottom: 1px solid #0056b3;
  }
  
  .userEntity-header h2 {
    margin: 0;
  }
  
  .userEntity-header button {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    border-radius: 4px;
    font-size: 16px;
  }
  
  .userEntity-header button:hover {
    background-color: #d32f2f;
  }
  
  .userEntity-header a {
    color: white;
    text-decoration: none;
}

  .userEntity-header a:hover {
    text-decoration: underline;
}
