.chat-list {
    width: 25%;
    padding: 20px;
    background-color: #f9f9f9;
    border-right: 1px solid #ddd;
  }
  
  .chat-list h3 {
    margin-bottom: 15px;
  }
  
  .chat-list ul {
    list-style: none;
    padding: 0;
  }
  
  .chat-list li {
    padding: 10px;
    margin-bottom: 5px;
    background-color: #fff;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .chat-list li:hover {
    background-color: #eee;
  }
  