.chat-window {
    width: 75%;
    padding: 20px;
    background-color: #ffffff;
    border-left: 1px solid #ddd;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 60px); /* Вычитаем высоту заголовка */
  }
  
  .chat-window h3 {
    margin: 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #ddd;
  }
  
  .messages {
    flex: 1;
    overflow-y: auto;
    padding-right: 10px; /* Отступ справа для скролла */
  }
  
  .message {
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 5px;
  }
  
  .message.sent {
    background-color: #e0ffe0;
    text-align: right;
  }
  
  .message.received {
    background-color: #f1f0f0;
  }
  
  input[type="text"] {
    margin-top: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 100%;
  }
  