/* Global Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
}

/* Login Page Container */
.login-page {
    width: 300px;
    margin: 50px auto;
    padding: 20px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Login Form */
.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* Form Labels */
.login-form label {
    margin-bottom: 10px;
    font-weight: bold;
}

/* Form Inputs */
.login-form input {
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* Form Button */
.login-form button[type="submit"] {
    width: 100%;
    height: 40px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Form Button Hover */
.login-form button[type="submit"]:hover {
    background-color: #0056b3;
}

/* Error Messages */
.error-message {
    color: red;
    margin-bottom: 10px;
}